
import { fastfoodFeature } from '@/interfaces'
import { computed, defineComponent, PropType, ref } from 'vue'

export default defineComponent({
    props: {
        curLocation: {
            type: Object as PropType<fastfoodFeature>,
        },
        locationIndex: {
            type: Number
        },
        finished: {
            type: Boolean,
            default: () => {
                return false
            }
        },
        guessDistance: {
            type: Number,
            default: () => {
                return 0
            }
        }
    },
    setup(props, {emit}) {

        const guessButton = ref<boolean>(true)
        const distance = computed(() => {
            if (props.guessDistance < 1) {
                return `${Math.round(props.guessDistance * 1000)} m`;
            } else {
                return `${Math.round(props.guessDistance * 10) / 10} km`
            }
        })

        const guessUpdate = () => {
            emit('guessUpdate');
            guessButton.value = !guessButton.value
        }

        const nextQuestion = () => {
            guessButton.value = !guessButton.value
            emit('nextQuestion')
        }

        const finishQuiz = () => {
            emit('finishQuiz')
        }

        return {
            guessUpdate,
            guessButton,
            nextQuestion,
            finishQuiz,
            distance
        }
    }
})
